<style lang="less">
    @import './transfer.less';
</style>
<template>
    <div class="transfer-main">
        <div class="lookup-con">
            <!-- <Select v-model="lookup.asset_id" placeholder="全部资产" style="width:150px;">
                <Option v-for="item in assets" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
            <Select v-model="lookup.type_id" placeholder="全部交易"  style="width:150px;">
                <Option v-for="item in types" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select> -->
            <Button  type="primary" @click="handlerLookup(1)" >{{$t('page_transfer.btn.refresh')}}</Button>
        </div>
        <Table ref="table" :data="tableData" :columns="tableColumns" stripe :height="tableHeight"></Table>
        <div style="margin: 10px;overflow: hidden">
            <div style="text-align:center">
                <Page :total="lookup.transfer_total" :current="lookup.page_index" @on-change="changePage"></Page>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name:'transfer',
        data() {
            return {
                tableHeight:400,
                types:[],
                assets:[],
                lookup:{
                    transfer_total:1,
                    page_index:1,
                    page_size:200,
                    type_id:'',
                    asset_id:''
                },
                tableData: [],
                tableColumns: [
                    {
                        title: 'UNIT',
                        key: 'unit_hash'
                    },
                    {
                        title: 'FROM',
                        key: 'from_addr'
                    },
                    {
                        title: 'TO',
                        key: 'to_addr'
                    },
                    {
                        title: 'AMOUNT',
                        key: 'amount',
                        width:100,
                        render: (h, params) => {
                            return h('div', this.formatAmount(this.tableData[params.index].amount,this.tableData[params.index].to_addr));
                        }
                    },
                    {
                        title: 'TIME',
                        key: 'time',
                        width:150,
                        render: (h, params) => {
                            return h('div', this.formatDate(this.tableData[params.index].time));
                        }
                    }
                ]
            };
        },
        methods: {
            init(){
                this.assets=[
                    {id:'1',name:'SDG'},
                    // {id:'2',name:'DJS'},
                    // {id:'3',name:'IDSE'},
                    // {id:'4',name:'DSA'},
                ];
                this.types=[
                    {id:'1',name:'发送'},
                    {id:'2',name:'接收'}
                ]
                this.handlerLookup(1);
            },
            handlerLookup(page_index) {
                this.tableData=[];
                let w=this.$store.state.wallet;
                w.getTxs().then(
                    (rep)=>{
                        this.tableData=rep;  
                    },
                    (error)=>{
                        console.log('get txs---',error);
                    }
                );     
            },
            formatDate (timestamp) {
                var unixtimestamp = new Date(timestamp*1000);
                var year = 1900 + unixtimestamp.getYear();
                var month = "0" + (unixtimestamp.getMonth() + 1);
                var date = "0" + unixtimestamp.getDate();
                var hour = "0" + unixtimestamp.getHours();
                var minute = "0" + unixtimestamp.getMinutes();
                var second = "0" + unixtimestamp.getSeconds();
                return year + "-" + month.substring(month.length-2, month.length)  + "-" + date.substring(date.length-2, date.length)
                    + " " + hour.substring(hour.length-2, hour.length) + ":"
                    + minute.substring(minute.length-2, minute.length) + ":"
                    + second.substring(second.length-2, second.length);
            },
            formatAmount (amount,to_addr) {
                if(this.$store.state.wallet.mainAddress==to_addr)
                {
                    return '+'+amount/1000000;
                }
                else
                {
                    return '-'+amount/1000000;
                }
                return amount/1000000;
            },
            changePage (i) {
                this.handlerLookup(i);
            }
            
        },
        mounted() {
            this.init();
            //window height - table height - page control row height
            this.tableHeight = window.innerHeight - this.$refs.table.$el.offsetTop - 60
        },
        activated() {
            this.handlerLookup(1);
        },
    };
</script>


